/* ELEMENT STYLES -------------------- */

.aboutScoreboard p {
    padding-top: 10%;
    margin: auto;
    font-size: 1.25em;
    line-height: 1.6;
    text-align: left;
    padding-bottom: 15%;
}

  form {
    display: flex;
    background-color: #8063FA;
  }
  
  input[type=text] {
    flex-grow: 1;
    border-width: 0 0 1px 0;
    margin: 15px 10px 15px 15px;
    padding: 10px;
    border-radius: 5px;
    background-color: #5B45B9;
    border-style: none;
    text-shadow: none;
    text-transform: uppercase;
    color: #C7BCF8;
    letter-spacing: 2px;
    outline: none;
  }
  
  input[type=text]::-webkit-input-placeholder{
    color: #fff;
    letter-spacing: 2px;
  }
  
  input[type=text]:focus{
    background-color: rgba(0, 0, 0, 0.2);
  }
  
  input[type=submit] {
    display: block;
    font-size: .6em;
    margin: 15px 15px 15px 0;
    padding: 10px;
    background-color: #44338D;
    border-radius: 5px;
    border: none;
    color: #fff;
    letter-spacing: 2px;
    font-weight: bold;
    text-shadow: none;
    text-transform: uppercase;
    cursor: pointer;
  }
  
  button,
  input[type=submit] {
    outline: none;
    transition: background-color 0.2s ease,
                color 0.2s ease;
  }
  
  /* ICONS */
  
  svg {
    width: 28px;
    height: 19px;
    margin-right: 5px;
    fill: #F0F0F0;
  }
  
  .is-high-score {
    fill: #FFC657;
    animation: grow 0.35s ease-in-out;
    transform-origin: 50% 50%;
  }
  
  @keyframes grow {
    50% { transform: scale(1.4) rotate(-3deg); }
  }
  
  /* COMPONENT STYLES -------------------- */
  
  .scoreboard {
    background: #fff;
    width: 60%;
    margin: 70px auto;
    border-radius: 6px;
    overflow: hidden;
    padding-top: 7%;
  }
  
  .player {
    text-align: left;
    display: flex;
    font-size: 1.2em;
    border-bottom: solid 2px #EEEEEE;
    letter-spacing: 2px;
    
  }
  
    .player:last-of-type {
      border-bottom-color: #D4CAFF;
    }
  
    .player-name:hover .remove-player {
      visibility: visible;
    }
  
    .player-name,
    .counter-score {
      color: #2F2366;
    }
  
    .player-name {
      flex-grow: 1;
      line-height: 3.5em;
      padding-left: 10px;
      font-weight: bold;
    }
  
    .remove-player {
      font-size: 1.15em;
      border: none;
      outline: none;
      visibility: hidden;
      color: #EF5350;
      cursor: pointer;
      margin: 0 10px 0 0;
      padding: 0;
      background: none;
    }
  
  .stats,
  .stopwatch {
    width: 170px;
  }
  
  .stats {
    margin: 0 0 0 28px;
    font-weight: normal;
  }
  
    .stats td:first-child {
      text-align: right;
      font-weight: normal;
      letter-spacing: 2px;
      font-size: .7em;
    }
  
    .stats td:last-child {
      text-align: left;
    }
  
  .stopwatch {
    padding: 15px 10px 5px 10px;
    margin: -5px -10px -5px 10px;
    background: #5B45B9;
  }
  
    .stopwatch-time {
      display: block;
      font-family: monospace;
      font-size: 2.15em;
    }
  
    .stopwatch button {
      margin: 8px 5px;
      background-color: #44338D;
      border-radius: 5px;
      padding: 7px 8px;
      border: none;
      color: #fff;
      letter-spacing: 2px;
      font-weight: bold;
      text-shadow: none;
      text-transform: uppercase;
    }
  
    .stopwatch button:hover,
    input[type=submit]:hover {
      background: #fff;
      color: #44338D;
      cursor: pointer;
    }
  
    .stopwatch h2 {
      font-size: .6em;
      margin: 0;
      font-weight: normal;
      letter-spacing: 2px;
    }
  
  .counter {
    display: flex;
    width: 190px;
  }
  
    .counter-action {
      border: none;
      font-size: 1.15em;
      font-weight: bold;
      color: #B0AEBA;
      background-color: #DCDCDC;
      display: block;
      padding: 20px 20px;
      cursor: pointer;
      outline: none;
    }
  
    .counter-action:hover {
      color: #fff;
    }
  
    .increment:hover {
      background: #83D97C;
    }
  
    .decrement:hover {
      background: #EF5350;
    }
  
    .counter-score {
      flex-grow: 1;
      text-align: center;
      font-family: monospace;
      font-size: 2em;
      line-height: 2em;
    }
  
/* =================================
    Media Queries
==================================== */
  @media screen and (max-width: 480px) {
  
    .scoreboard {
      width: 100%;
    }

    .aboutScoreboard {
      font-size: 1.25em;
      line-height: 1.6;
      padding-bottom: 15%;
      
    }

    .aboutScoreboard p{
      padding-left: 2%;
      padding-right: 2%;
    }
  }
  